import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  constructor(
  ) { }
  ngOnInit() {
    const url = {
      url: 'https://d3cfn34s6l242i.cloudfront.net/news-paper/Sakshi5a1001781dfc8c4d789cd3b4/7ms81531353087825-page_full.pdf',
      password: 'NWExMDAxNzgxZGZjOGM0ZDc4OWNkM2I0'
    };
    const pdfjsLib = window['pdfjs-dist/build/pdf'];

      let totalTimeTaken;
      var abc = "abc";
      let pdfDoc = null,
      pageNum = 1,
      pageRendering = false,
      pageNumPending = null,
      scale = 0.5,
      canvas: any = document.getElementById('the-canvas'),
      ctx = canvas.getContext('2d');


    function renderPage(num) {
      var t0 = performance.now(); 
      var t1;
      pageRendering = true;
      pdfDoc.getPage(num).then(function (page) {
        let viewport = page.getViewport(scale);
        canvas.height = viewport.height;
        canvas.width = viewport.width;
        let renderContext = {
          canvasContext: ctx,
          viewport: viewport
        };
        let renderTask = page.render(renderContext);
        renderTask.then(function () {
          console.log('Page rendered');
          pageRenderedT1(t0)
        });
     


        renderTask.promise.then(function () {
          pageRendering = false;
          if (pageNumPending !== null) {
            renderPage(pageNumPending);
            pageNumPending = null;
          }
        });
      });

      document.getElementById('page_num').textContent = num;
     
    }

    function  pageRenderedT1(t0){
      var t1 = performance.now(); 
      totalTimeTaken = (t1-t0)/1000;
     // alert('page rendered in ' + totalTimeTaken + ' seconds')
     document.getElementById('time').textContent = totalTimeTaken;
    }


    function queueRenderPage(num) {
      if (pageRendering) {
        pageNumPending = num;
      } else {
        renderPage(num);
      }
    }


    function onPrevPage() {
      if (pageNum <= 1) {
        return;
      }
      pageNum--;
      queueRenderPage(pageNum);
    }
    document.getElementById('prev').addEventListener('click', onPrevPage);


    function onNextPage() {
      if (pageNum >= pdfDoc.numPages) {
        return;
      }
      pageNum++;
      queueRenderPage(pageNum);
    }
    document.getElementById('next').addEventListener('click', onNextPage);


    pdfjsLib.getDocument(url).then(function (pdfDoc_) {
      pdfDoc = pdfDoc_;
      document.getElementById('page_count').textContent = pdfDoc.numPages;
      renderPage(pageNum);
    });



  }
}



